:root {
  --font-lato: "Lato", sans-serif;
  --font-raleway: "Raleway", sans-serif;
}

@font-face {
  font-family: "Lato";
  font-weight: 600;
  src: local("Lato"), url("./app/fonts/Lato-SemiBold.woff2") format("woff2"), url("./app/fonts/Lato-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Lato";
  font-weight: 800;
  src: local("Lato"), url("./app/fonts/Lato-Black.woff2") format("woff2"), url("./app/fonts/Lato-Black.woff") format("woff");
}
:root {
  --color--white: #ffffff;
  --color--black: #000000;
  --color-primary: #3E9CDC;
  --color-secondary: #272733;
  --color-accent: #43ABF0;
  --color-hero-button: #2A2B30;
  --color-hero-border: #6F6D7B;
  --color-subtitle: #BBBBBB;
  --color-order-form: #C3C3C3;
  --color-feature-border: #286690;
  --bg-color-header: rgba(16, 16, 29, 0.9);
  --bg-color-footer: #3D4050;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}
html ::-webkit-scrollbar {
  width: 7px;
  height: 53px;
  background: #3D4050;
}
html ::-webkit-scrollbar-thumb {
  background-color: #3E9CDC;
  border-radius: 3px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-lato);
  color: var(--color--black);
}

ul {
  padding: 0;
}

li {
  list-style-type: none;
}

p {
  padding: 0;
  margin: 0;
}

.accent {
  font-weight: 800;
  color: var(--color-accent);
}

.uppercase {
  text-transform: uppercase;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: ) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (max-width: 39.9375em) {
  .container {
    padding: 0 1.25rem;
  }
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 6.25rem;
  background-color: var(--bg-color-header);
  z-index: 1;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.header__menu {
  display: flex;
  gap: 3.75rem;
}
@media (max-width: 63.9375em) {
  .header__menu {
    display: none;
  }
}
.header__menu__mobile {
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
@media (min-width: 64em) {
  .header__menu__mobile {
    display: none;
  }
}
.header__logo {
  cursor: pointer;
}
@media (max-width: 39.9375em) {
  .header__logo {
    width: 12.5rem;
    object-fit: contain;
  }
}

.menu__item {
  font-weight: 600;
  font-size: 0.8125rem;
  line-height: 1rem;
  text-transform: uppercase;
  cursor: pointer;
}
.menu__item > a {
  text-decoration: none;
  color: var(--color--white);
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 0 0.125rem;
  transition: background-size 0.2s;
}
.menu__item > a:hover {
  background-size: 100% 0.125rem;
}

.menu__item.active > a {
  color: var(--color-accent);
}

.hero {
  width: 100%;
  height: 53.125rem;
  position: relative;
  padding: 0 0 30% 0;
}
@media (max-width: 39.9375em) {
  .hero {
    display: flex;
    justify-content: center;
    height: 46.875rem;
  }
}
.hero > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.hero__wrapper {
  padding-top: 12.6875rem;
  height: 27.75rem;
}
.hero__title {
  max-width: 48.75rem;
  font-weight: 700;
  font-size: 3rem;
  line-height: 3.625rem;
  text-transform: uppercase;
  color: var(--color--black);
  margin-bottom: 5rem;
}
@media (max-width: 74.9375em) {
  .hero__title {
    font-size: 2.25rem;
    line-height: 2.875rem;
    margin-bottom: 3.75rem;
  }
}
@media (max-width: 39.9375em) {
  .hero__title {
    max-width: 100%;
    font-size: 1.125rem;
    line-height: 1.5rem;
    margin-bottom: 1.25rem;
  }
}
.hero__title > span {
  font-family: var(--font-raleway);
}
.hero__description {
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.5rem;
  color: var(--color--black);
}
@media (max-width: 74.9375em) {
  .hero__description {
    font-size: 1rem;
    line-height: 1.25rem;
  }
}
@media (max-width: 39.9375em) {
  .hero__description {
    font-size: 0.875rem;
    line-height: 1rem;
  }
}
.hero__description__list {
  font-weight: 400;
  margin-bottom: 5rem;
}
@media (max-width: 74.9375em) {
  .hero__description__list {
    margin-bottom: 3.75rem;
  }
}
@media (max-width: 39.9375em) {
  .hero__description__list {
    margin-bottom: 2.5rem;
  }
}
.hero__actions {
  display: flex;
  gap: 1.875rem;
}
@media (max-width: 39.9375em) {
  .hero__actions {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.875rem;
  }
}
.hero__actions__btn {
  font-family: var(--font-lato);
  width: 11.4375rem;
  height: 2.6875rem;
  font-weight: 400;
  font-size: 0.8125rem;
  line-height: 1rem;
  text-transform: uppercase;
  padding: 0 2rem;
  border-radius: 0.25rem;
  cursor: pointer;
}
@media (max-width: 39.9375em) {
  .hero__actions__btn {
    width: 100%;
  }
}
.hero__actions__btn:first-child {
  color: var(--color--white);
  background: var(--color-accent);
  border: 0.0625rem solid var(--color--white);
}
.hero__actions__btn:last-child {
  color: var(--color-hero-button);
  background: var(--color--white);
  border: 0.0625rem solid var(--color-hero-border);
}
.hero__actions__btn:hover {
  background: var(--color-primary);
  color: var(--color--white);
}

.list__item {
  list-style-type: "-";
  padding-left: 0.625rem;
  margin-left: 1.25rem;
}

.order {
  height: 57.5rem;
  position: relative;
  background-image: url("order-bg.jpeg");
  background-color: var(--color-secondary);
  background-blend-mode: color-burn;
  padding-top: 7.5rem;
  padding-bottom: 7.5rem;
}
@media (max-width: 74.9375em) {
  .order {
    height: 100%;
  }
}
.order__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--color--white);
}
.order__text {
  max-width: 34.6875rem;
  text-transform: uppercase;
  margin-bottom: 5rem;
}
.order__text__title {
  text-align: center;
  font-weight: 800;
  font-size: 2.25rem;
  line-height: 2.6875rem;
  margin-bottom: 1.875rem;
}
@media (max-width: 74.9375em) {
  .order__text__title {
    font-size: 1.25rem;
    line-height: 1.5rem;
  }
}
.order__text__subtitle {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.375rem;
  color: var(--color-subtitle);
}
@media (max-width: 74.9375em) {
  .order__text__subtitle {
    text-align: center;
    font-size: 0.75rem;
    line-height: 1.125rem;
  }
}
.order__block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}
@media (max-width: 74.9375em) {
  .order__block {
    flex-direction: row;
    align-items: flex-start;
  }
}
@media (max-width: 63.9375em) {
  .order__block {
    align-items: center;
    flex-direction: column;
  }
}
.order__features {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 5rem;
}
@media (max-width: 74.9375em) {
  .order__features {
    justify-content: flex-start;
    max-width: 25rem;
  }
}
@media (max-width: 63.9375em) {
  .order__features {
    justify-content: center;
    max-width: 25rem;
  }
}
@media (max-width: 39.9375em) {
  .order__features {
    justify-content: flex-start;
    flex-direction: column;
  }
}
.order__features__list {
  display: flex;
  justify-content: center;
}
@media (max-width: 74.9375em) {
  .order__features__list {
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
  }
}
.order__form {
  font-family: var(--font-lato);
  font-weight: 400;
  font-size: 1.125rem;
  width: 100%;
  max-width: 73.75rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "system email name" "range range file" "... submit ...";
  grid-gap: 1.875rem;
}
@media (max-width: 74.9375em) {
  .order__form {
    grid-template-columns: 1fr;
    grid-template-areas: "system" "email" "name" "range" "file" "submit";
    grid-gap: 3.875rem;
    padding: 1.25rem;
  }
}
@media (max-width: 39.9375em) {
  .order__form {
    padding: 0;
  }
}

.feature__item {
  position: relative;
}
.feature__item:not(:first-child)::before {
  background: radial-gradient(circle, #c4c4c4 3px, transparent 0) 100% 0 repeat-x;
  background-size: 1.625rem 0.375rem;
  content: " ";
  display: block;
  height: 0.375rem;
  left: -4.375rem;
  position: absolute;
  top: 3.125rem;
  width: 7.5rem;
}
@media (max-width: 74.9375em) {
  .feature__item:not(:first-child)::before {
    background-repeat: repeat-y;
    background-position: center;
    background-size: 0.375rem 1.3125rem;
    top: unset;
    bottom: 100%;
    left: 2.375rem;
    width: 0.375rem;
    height: 3.75rem;
  }
}
.feature__item + .feature__item {
  margin-left: 1.875rem;
}
.feature__item__desc {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.1875rem;
  text-align: center;
  width: 12.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--color--white);
}
.feature__item__desc:before {
  content: "";
  display: block;
  width: 6.25rem;
  height: 6.25rem;
  border-radius: 50%;
  border: 0.3125rem solid var(--color-feature-border);
  background-color: var(--color-primary);
  margin-bottom: 1.25rem;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}
@media (max-width: 74.9375em) {
  .feature__item__desc:before {
    margin-bottom: 0;
    margin-right: 0.75rem;
    width: 5rem;
    height: 5rem;
    flex: 0 0 auto;
  }
}
@media (max-width: 74.9375em) {
  .feature__item__desc {
    flex-direction: row;
    width: 15rem;
    text-align: left;
    margin-bottom: 3.75rem;
  }
}

.feature__item:nth-child(1) .feature__item__desc::before {
  background-image: url("icons/feature-magnifier.svg");
}

.feature__item:nth-child(2) .feature__item__desc::before {
  background-image: url("icons/feature-percent.svg");
}

.feature__item:nth-child(3) .feature__item__desc::before {
  background-image: url("icons/feature-document.svg");
}

.feature__item:nth-child(4) .feature__item__desc::before {
  background-image: url("icons/feature-mail.svg");
}

.feature__item:nth-child(5) .feature__item__desc::before {
  background-image: url("icons/feature-money.svg");
}

.dataset {
  border: 0.0625rem solid var(--bg-color-footer);
  background-color: rgba(var(--color-secondary), 0.9);
  border-radius: 0 0 0.1875rem 0.1875rem;
  padding: 0.25rem;
}
.dataset__list {
  height: 100%;
  overflow-y: scroll;
}
.dataset__item {
  color: var(--color--white);
  cursor: pointer;
  padding: 0.5rem 0.625rem;
}
.dataset__item:hover {
  color: var(--color-accent);
}

.form__select-wrap {
  grid-area: system;
  position: relative;
  z-index: 1;
}
.form__select {
  display: flex;
  align-items: center;
  width: 23.125rem;
  height: 3rem;
  background: rgba(255, 255, 255, 0.85);
  color: var(--color-secondary);
  border: 0.0625rem solid var(--color--white);
  border-radius: 0.1875rem;
  padding: 0.625rem;
  cursor: pointer;
  transition: all 0.1s linear;
  font-size: 1.125rem;
  line-height: 1.375rem;
}
@media (max-width: 74.9375em) {
  .form__select {
    width: 100%;
  }
}
.form__select::placeholder {
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.375rem;
  color: var(--color-secondary);
}
.form__select--open {
  background-color: rgba(var(--bg-color-footer), 0.85);
  border: 0.1rem solid var(--bg-color-footer);
  color: #ccc;
  border-radius: 0.1875rem 0.1875rem 0 0;
}
.form__select--open::placeholder {
  color: var(--color--white);
}
.form__dataset {
  width: 23.125rem;
  height: 14.25rem;
  overflow-y: hidden;
  position: absolute;
  top: 100%;
  visibility: hidden;
  background: #3D4050;
  transition: all 0.1s linear;
}
@media (max-width: 74.9375em) {
  .form__dataset {
    width: 100%;
    height: 20rem;
  }
}
.form__select--open ~ .form__dataset {
  visibility: visible;
}
.form__select-arrow {
  background: url("icons/arrow.svg") center no-repeat;
  width: 1.25rem;
  height: 1.25rem;
  position: absolute;
  cursor: pointer;
  top: 0.875rem;
  right: 0.75rem;
  pointer-events: none;
  transition: all 0.2s linear;
}
.form__select--open + .form__select-arrow {
  background: url("icons/arrow-white.svg") center no-repeat;
  color: var(--color--white);
}
.form__input {
  width: 23.125rem;
  height: 3rem;
  background: rgba(255, 255, 255, 0.85);
  color: var(--color-secondary);
  border: 0.0625rem solid var(--color--white);
  border-radius: 0.1875rem;
  padding: 0.75rem 0.625rem;
  font-size: 1.125rem;
  line-height: 1.375rem;
}
@media (max-width: 74.9375em) {
  .form__input {
    width: 100%;
  }
}
.form__input::placeholder {
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.375rem;
  color: var(--color-secondary);
}
.form__input--email {
  grid-area: email;
}
.form__input--name {
  grid-area: name;
}
.form__input-wrap {
  width: 100%;
}
.form__input-wrap--range {
  grid-area: range;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-content: space-between;
}
@media (max-width: 74.9375em) {
  .form__input-wrap--range {
    width: 100%;
    min-height: 3.125rem;
  }
}
@media (max-width: 63.9375em) {
  .form__input-wrap--range {
    min-height: 5rem;
  }
}
.form__input-wrap--file {
  grid-area: file;
}
.form__range-label {
  color: var(--color--white);
}
.form__range-value {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-weight: 600;
  color: var(--color--white);
}
.form__range {
  display: block;
  width: 100%;
  position: relative;
  background-color: #dfdfe1;
  border: 0.0625rem solid var(--color--white);
  height: 0.375rem;
  border-radius: 0.1875rem;
  cursor: pointer;
}
.form__thumb {
  background-color: #42A9ED;
  border: 0.1875rem solid var(--color-feature-border);
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  position: absolute;
  top: -0.625rem;
}
.form__file-label {
  font-weight: 600;
  border: 0.125rem solid var(--color-primary);
  padding: 0.625rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background-color: var(--color--white);
  color: var(--color-secondary);
  font-size: 0.9375rem;
  line-height: 1.125rem;
  text-transform: uppercase;
  border-radius: 0.1875rem;
}
.form__file-label::before {
  content: "";
  display: inline-block;
  width: 1.125rem;
  height: 1.25rem;
  background: url("icons/file.svg") center no-repeat;
  margin-right: 0.625rem;
}
.form__file-label:hover {
  background: var(--color-accent);
  color: var(--color--white);
}
.form__file {
  display: none;
}
.form__submit {
  width: 100%;
  max-width: 23.125rem;
  height: 3rem;
  background: var(--color-primary);
  margin-top: 1.875rem;
  font-weight: 600;
  font-size: 0.9375rem;
  line-height: 1.125rem;
  border: 0.0625rem solid var(--color--white);
  border-radius: 0.25rem;
  color: var(--color--white);
  cursor: pointer;
  text-transform: uppercase;
  grid-area: submit;
}
.form__submit:hover {
  background-color: var(--color-accent);
  color: var(--color--white);
}

.footer {
  background: var(--bg-color-footer);
  margin-top: auto;
  width: 100%;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 400;
  font-size: 0.8125rem;
  line-height: 1rem;
  color: var(--color--white);
  width: 100%;
  height: 100%;
  max-height: 5rem;
}
@media (max-width: 74.9375em) {
  .footer__wrapper {
    max-height: 100%;
    padding-right: 3rem;
    padding-left: 3rem;
  }
}
@media (max-width: 63.9375em) {
  .footer__wrapper {
    height: auto;
    flex-direction: column-reverse;
  }
}
@media (max-width: 39.9375em) {
  .footer__copyright {
    max-width: 11.25rem;
  }
}
.footer__separator {
  background-color: #C3C3C3;
  width: 0.0625rem;
  height: 1.125rem;
}
@media (max-width: 63.9375em) {
  .footer__separator {
    transform: rotate(90deg);
    height: 3.75rem;
    margin-bottom: 1.5rem;
  }
}
.footer__payments__list {
  display: flex;
  align-items: center;
  gap: 2.375rem;
}
@media (max-width: 74.9375em) {
  .footer__payments__list {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
}
@media (max-width: 63.9375em) {
  .footer__payments__list {
    flex-direction: row;
  }
}
.footer__contacts__list {
  display: flex;
  align-items: center;
  gap: 2.375rem;
}
@media (max-width: 74.9375em) {
  .footer__contacts__list {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
}

.payment__item {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  font-weight: 400;
  font-size: 0.8125rem;
  line-height: 1rem;
  color: var(--color-order-form);
}

.contact__item {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  font-weight: 400;
  font-size: 0.8125rem;
  line-height: 1rem;
}
.contact__item__link {
  color: var(--color-white);
}
.contact__item__link:hover {
  color: var(--color-accent);
}