//640px, 1024px, 1200px
$breakpoints-up: (
        'small': '40em',
        'medium': '64em',
        'xlarge': '75em',
);

// 639px, 1023px, 1199px
$breakpoints-down: (
        'small': '39.9375em',
        'medium': '63.9375em',
        'large': '74.9375em',
);

@mixin breakpoint($size) {
  @media (min-width: map-get($breakpoints-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}
